.clock {
  width: 80vmin;
  height: 80vmin;
  border: 3vmin solid $mainColor;
  border-radius: 50%;
  position: relative;
  box-shadow: inset 0 0 14px rgb(122, 122, 122);
  background-color: $mainColor;

  &:before {
    content: "";
    @include circle(8vmin, $center-color);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
  }

  &__arrow {
    position: absolute;
    top: 50%;
    left: calc(50% - 5px);
    transform-origin: top;

    &--green {
      height: 36vmin;
      width: 2vmin;
      background-color: $minuteHandColor;
      transform: translateX(-50%);
      z-index: 1;
      border-radius: 10px;
      background: linear-gradient(to bottom, rgba(196, 5, 5, 0) 0%, rgb(173, 16, 16) 100%);
      animation: rotate $durationSecond infinite linear;
    }

    &--blue {
      height: 25vmin;
      width: 2.5vmin;
      animation: rotate $durationMinute infinite linear;
      z-index: 1;
      border-radius: 10px;
      &:before {
        content: "";
        position: absolute;
        bottom: -2vmin;
        left: 50%;
        transform: translateX(-50%);
        border-style: solid;
        border-width: 30vmin 3.20vmin 0 1.25vmin;
        border-color: rgb(60, 255, 0) transparent transparent transparent;
        transform-origin: top;
      }
    }
  }

  @keyframes rotate {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg)
    }
  }
}
